@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
html, body, #root {
  height: 100%;
}


body {
  font-family: "Roboto Condensed" , sans-serif;
}



.header-wrapper {
  position: relative;
  background: url(/my-app/static/media/laptop_and_wall_background.6bc04afa.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.main-info h1 {
  color:white;
  font-weight: bold;
  text-transform: uppercase;
  text-shadow: 1px 1px 0 #000,
    -1px 1px 0 #000,
    1px -1px 0 #000,
    -1px -1px 0 #000,
    0px 1px 0 #000,
    0px -1px 0 #000,
    -1px 0px 0 #000,
    1px 0px 0 #000,
    2px 2px 0 #000,
    -2px 2px 0 #000,
    2px -2px 0 #000,
    -2px -2px 0 #000,
    0px 2px 0 #000,
    0px -2px 0 #000,
    -2px 0px 0 #000,
    2px 0px 0 #000,
    1px 2px 0 #000,
    -1px 2px 0 #000,
    1px -2px 0 #000,
    -1px -2px 0 #000,
    2px 1px 0 #000,
    -2px 1px 0 #000,
    2px -1px 0 #000,
    -2px -1px 0 #000;
}

.typed-text {
  font-size: 2rem;
  color: #30D5C8;
  text-shadow: 1px 1px 0 #000,
    -1px 1px 0 #000,
    1px -1px 0 #000,
    -1px -1px 0 #000,
    0px 1px 0 #000,
    0px -1px 0 #000,
    -1px 0px 0 #000,
    1px 0px 0 #000,
    2px 2px 0 #000,
    -2px 2px 0 #000,
    2px -2px 0 #000,
    -2px -2px 0 #000,
    0px 2px 0 #000,
    0px -2px 0 #000,
    -2px 0px 0 #000,
    2px 0px 0 #000,
    1px 2px 0 #000,
    -1px 2px 0 #000,
    1px -2px 0 #000,
    -1px -2px 0 #000,
    2px 1px 0 #000,
    -2px 1px 0 #000,
    2px -1px 0 #000,
    -2px -1px 0 #000;
}

.footer {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
}


.form {
    margin-left: 10px;
  }

input {
  margin-bottom: 5px;
}

.btn {
  background-color: #30D5C8;
  font-weight: bold;
}

.columns {
  display: flex;
  border-top: solid #554C55 5px;
}

.work {
  color: #554C55;
  font-size: 250%;
  border-right: solid #554C55 8px;
  text-align: right;
  word-wrap:normal;
  padding:60px 8px 50px 8px;
  margin: 10px;
}

.work {
  padding:60px 8px 1000px 8px;
}

.myWork .workbig {
  flex: 1 1;
  display: block;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: flex-start;
  position: relative;
}

.workbig {
  border-style: solid;
  border-width: 5px;
  margin: 20px 20px 20px 0px;
}

.workbig img {
  width: 100%;
}

.myWork .worksmall {
  flex: 4 1;
  display: inline-block;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: flex-start;
  position: relative;
}

.worksmall {
  border-style: solid;
  border-width: 5px;
  margin: 20px 20px 20px 0px;
}

.worksmall img {
  width: 100%;
}

.workinfo {
  position: absolute;
  background-color: #554C55;
  color: #30D5C8;
  height: 55px;
  width: 280px;
  bottom: 10px;
  left: 10px;
}
